import * as React from "react";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";
import Banner from "src/components/Banner";

export interface DangsanProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 50px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 60px;
        & > div:nth-child(1) {
            width: calc(20% - 45px);
            margin-right: 45px;

            font-size: 18px;
            font-weight: bold;
            font-style: normal;
            font-stretch: normal;
            line-height: 1.61;
            letter-spacing: normal;
            color: #000000;
        }
        & > div:nth-child(2) {
            width: 80%;
            & > h1 {
                margin-bottom: 34px;
                font-size: 32px;
                font-weight: bold;
                font-style: normal;
                font-stretch: normal;
                line-height: normal;
                letter-spacing: normal;
                color: #000000;
            }

            & > p {
                font-size: 14px;
                font-weight: normal;
                font-style: normal;
                font-stretch: normal;
                line-height: 1.71;
                letter-spacing: normal;
            }

            & > img {
                width: 100%;
                height: 430px;
                background-color: #aaa;
            }
        }
    }
`;

export default class Dangsan extends React.Component<DangsanProps, any> {
    public render() {
        return (
            <>
                <Navigation
                    list={[{ name: "홈", link: "/" }, { name: "프로젝트", link: "/project/" }, { name: "부동산 개발", link: "/project" }]}
                />
                <Content>
                    <Banner>
                        <img src={require("../../assets/project/dangsan.png")} alt="" />
                    </Banner>
                    <Desc>
                        <div>
                            <div>당산 동화빌딩</div>
                            <div>
                                <h1>타일벽의 로맨스, 동화빌딩</h1>
                                <p>
                                    동화빌딩은 서울시 영등포구 당산로 241번지에 있다.
                                    <br />
                                    <br />
                                    영등포구 당산역 일대는 1984년 완공된 빛 바랜 지하철 2호선 역사를 중심으로 서울 서부권 교통의 중 심으로
                                    발전해왔다. <br />
                                    역사 3번 출구 앞에 자리한 동화빌딩은 회벽색 시멘트의 당산역과 조화를 이루며 단단한 자세로 자리를 지키고
                                    있다.
                                    <br />
                                    <br />
                                    당산역 일대에는 1990년 문을 연 현대직업전문학교가 있다. 이 학교는 대한민국 청년들의 역량 개발과 직업
                                    능력 개발을 목적으로 <br />
                                    설립되었다. 본래 동화빌딩은 이 학교의 소유였다. 청년들이 내일을 꿈꾸던 이 건물은 2018년,
                                    유니언플레이스에 의해 재해석되었다.
                                    <br />
                                    <br />
                                    실력 있는 목수를 꿈꾸던 학생들의 실습실로 활용되어 온 지하는 로컬피플의 건강을 얻는 공간으로, 외국인
                                    노동자들이 <br />
                                    희망을 배우던 2층과 3층은 청년들의 외국어 배움과 외식 창업을 준비하는 공간으로 각각 바뀌었다. <br />
                                    한강이 시원스레 내려다 보이고, 지하철이 내 품 안으로 들어오는 듯한 건물의 상층부 는 글로벌 청년들을 위한
                                    주거공간이 되었다.
                                    <br />
                                    <br />
                                    변하지 않는 것은 유행을 타지 않는다. 옛 건축물의 아름다움은 남기고 현대의 모던함을 간결하게 더한
                                    동화빌딩은 <br />
                                    과거와 현대의 아름다운 조화로움 속에 젊은 당산 그 맨 앞에 서 있다.
                                    <br />
                                </p>
                            </div>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



